.hero {
  padding: 130px 0 80px;
  position: relative;
  overflow-x: hidden;
  &::before {
    content: "Skin Recovery";
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    font-family: "DM Serif Text";
    white-space: nowrap;
    font-size: 500px;
    color: #f9f9f9;
    @include media(">d2k") {
      top: 50%;
      left: 50%;
      transform: translate3d(-50%, -50%, 0);
    }
  }
  &-slide {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .swiper-slide-prev,
  .swiper-slide-next{
    opacity: .1;
  }
  .swiper-slide-active{
    opacity: 1;
  }
  .slide {
    &_text {
      max-width: 100%;
      font-family: "DM Serif Text";
      color: #013e37;
      text-align: center;
      padding-bottom: 20px;
      h1 {
        font-size: 150px;
        @include media("<xl") {
          font-size: 120px;
        }
        @include media("<lg") {
          font-size: 80px;
        }
      }
      h2 {
        font-size: 20px;
      }
    }
    &_image {
      img {
        width: 100%;
       height: 371px;
      }
    }
  }
  .btn {
    position: absolute;
    left: 50%;
    bottom: 5%;
    transform: translateX(-50%);
  }
}
